.wrapper {
  width: 100%;
  /* text-align: center; */
  /* border: solid grey 5px; */
}

p {
  text-align: center;
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: 20pt;
  font-weight: 400;
  color: #444444;
  /* border: solid grey 1px; */
}

.currency-item-wrapper {
  max-width: 994px;
  /* border: solid rgb(219, 21, 21) 1px; */
  margin: auto;
}

.currency-item {
  display: inline-block;
  /* border: solid grey 1px; */
  width: 120px;
  /* height: 100px; */
  background-color: rgb(241, 241, 241);
  margin: 5px;
  padding: 5px;
  border: solid rgb(237, 237, 237) 1px;
  box-shadow: 0px 0px 6px #e9e8e8;
  border-radius: 10px;
}

.currency-item__currency-name {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: smaller;
  width: 80px;
  min-height: 40px;
  font-weight: 600;

  color: rgb(41, 41, 41);
}
.currency-item__currency-ticker {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-weight: 600;
  min-height: 25px;
  color: rgb(41, 41, 41);
  text-decoration: underline;
}

.currency-item__currency-previous-price {
    font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
    color: rgb(36, 43, 110);
    font-weight: 100;
    font-size: smaller;
}

.currency-item__currency-price {
  font-family: Impact, Haettenschweiler, "Arial Narrow Bold", sans-serif;
  font-size: large;
  color: rgb(36, 43, 110);
  display: inline-block;

}

.currency-item__currency-move {
    display: inline-block;
}

.currency-item__currency-move-up {
    margin-left: 3px;
    color: green;
}

.currency-item__currency-move-down {
    margin-left: 3px;
    color: red;
}

.footer {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: smaller;
  margin-top: 50px;
  text-align: center;
}
